
.page {
    background: rgb(109, 103, 103);
}
h1 {
    text-align: center
}


.gallery {
    -webkit-column-count:6;
    -moz-column-count:6;
    column-count:10;
    
    cursor: pointer;
    -webkit-column-width: 20%;
    -moz-column-width: 20%;
    column-width: 20%;
    padding: 150px;
   
    
    
}

.gallery .pictures  {
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease ;
    margin-bottom: 12px;
    height: 100%;
    
    
}



.gallery .pictures:hover {
    filter: opacity(.5);

}


@media (max-width: 780px) {
    .gallery {
        -webkit-column-count:10;
        -moz-column-count:10;
        column-count:10;
       
    }

    @media (max-width: 480px) {
        .gallery {
            -webkit-column-count:1;
            -moz-column-count:1;
            column-count:1;
           
        }
    
    }
}

.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top:0;
    left:0;
    display: flex;
    justify-content:center;
    align-items: center;
    background-color: rgba(82, 76, 76, 0.801);
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out ;
    visibility: hidden;
    opacity:0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999;
}
.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1)
}
.model img {
    width: auto;
    max-width: 100%;
    max-height: 100%;
    height: auto;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;
}

.model.open svg {
   position: fixed; 
   top: 10px;
   right: 10px;
   width: 2rem;
   height: 2rem;
   padding: 5px;
   background-color: rgba(0,0,0.4);
   color:rgba(255, 255, 255, 0.582);
   cursor: pointer;
}